/* eslint-disable react-hooks/exhaustive-deps */
import {Fragment, useContext, useEffect} from "react";
import RoomContext from "system/context/roomInfo/room-context";
import ChatContext from "pages/components/ui/ChatModule/system/ChatContextProvider";
import {getPlayAtName, getPlaySpeedName} from "pages/lobby/Left/MusicSelector/GamePlaySettings";

export default function SettingsChangeNotifier() {
    const ctx = useContext(RoomContext);
    const settings = ctx.room.header.settings;
    const chatCtx = useContext(ChatContext);

    useEffect(() => {
        if (settings.limitedCommunication) {
            chatCtx.localAnnounce(` .`);
            chatCtx.localAnnounce(`  `);
        } else {
            chatCtx.localAnnounce(" ");
        }
    }, [settings.limitedCommunication]);
    useEffect(() => {
        chatCtx.localAnnounce(`: ${getPlaySpeedName(+settings.speed)}`);
    }, [settings.speed]);
    useEffect(() => {
        chatCtx.localAnnounce(`: ${getPlayAtName(+settings.playAt)}`);
    }, [settings.playAt]);
    useEffect(() => {
        chatCtx.localAnnounce(`  : ${settings.songsPlay}`);
    }, [settings.songsPlay]);
    useEffect(() => {
        chatCtx.localAnnounce(`: ${settings.guessTime}`);
    }, [settings.guessTime]);

    return <Fragment/>;
}